@import './fonts';

@function is-number($value) {
  @return type-of($value) == 'number';
}

@function is-px($value) {
  @return is-number($value) and index('px', unit($value)) != null;
}

@function refPxToRem($size) {
  @return pxToRem($size, $default_browser_font_size * $root_font_proportion);
}

@function pxToRem($size, $ref) {
  @if not is-px($size) {
    @error '$size passed to pxToRem must be defined in terms of px.';
  }
  @if not is-px($ref) {
    @error '$ref passed to pxToRem must be defined in terms of px.';
  }
  @return ($size / $ref) * 1rem;
}
