@import '../../styles/fonts';
@import '../../styles/functions';

$header_font_size: refPxToRem(42px);
$header_letter_spacing: refPxToRem(2px);
$header_font_weight: 700;
$header_line_height: refPxToRem(48px);

%header-base {
  font-size: $header_font_size;
  font-weight: $header_font_weight;
  letter-spacing: $header_letter_spacing;
  line-height: $header_line_height;
  font-family: $font_primary_information;
  user-select: none;
}
