@import './fonts';
@import './colors';
@import './functions';

.font__voice--primary {
  font-family: $font_primary_voice;
  font-size: refPxToRem(18px);
  color: $color_neutral_mid;
}

.font__voice--primary-leadin {
  font-family: $font_primary_voice;
  font-size: refPxToRem(20px);
  color: $color_primary_1;
}

// font-variant
.font__variant--normal {
  font-variant: normal;
}

.font__variant--small-caps {
  font-variant: small-caps;
}

// font-style
.font__style--normal {
  font-style: normal;
}

.font__style--italic {
  font-style: italic;
}

.font__style--oblique {
  font-style: oblique;
}

// font-weight
.font__weight--normal {
  font-weight: normal;
}

.font__weight--bold {
  font-weight: bold;
}

.font__weight--bolder {
  font-weight: bolder;
}

.font__weight--lighter {
  font-weight: lighter;
}

.font__weight--100 {
  font-weight: 100;
}

.font__weight--200 {
  font-weight: 200;
}

.font__weight--300 {
  font-weight: 300;
}

.font__weight--400 {
  font-weight: 400;
}

.font__weight--500 {
  font-weight: 500;
}

.font__weight--600 {
  font-weight: 600;
}

.font__weight--700 {
  font-weight: 700;
}

.font__weight--800 {
  font-weight: 800;
}

.font__weight--900 {
  font-weight: 900;
}

// text-align
.font__text-align--center {
  text-align: center;
}

.font__text-align--left {
  text-align: left;
}

.font__text-align--right {
  text-align: right;
}

.font__text-align--justify {
  text-align: justify;
}
